import {ACTIONS} from './constants';

const toggleButton = ({action: {payload}, updateState}) => {
	let {isExplainabilityPopover} = payload;
	if(isExplainabilityPopover)
		updateState({
			path: 'isExplainabilityPopOverOpen',
			value: payload.value,
			operation: 'set'
		});
};

const actionHandlers = {
	[ACTIONS.POPOVER_OPENED]: toggleButton
};

export default actionHandlers;
