import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-highlighted-value';
import styles from './styles.scss';
import {t} from 'sn-translate';

const renderValue = (percentage) => {
	if(percentage < 0){
		return <now-highlighted-value
			className="explainability-popover-pf-percentage"
			label={percentage+'%'}
			icon=""
			color="critical"
			variant="tertiary">
		</now-highlighted-value>;
	}
	else if(percentage > 0){
		return <now-highlighted-value
			className="explainability-popover-pf-percentage"
			label={'+'+percentage+'%'}
			icon=""
			color="positive"
			variant="tertiary">
		</now-highlighted-value>;
	}
	return <span className="explainability-popover-pf-percentage">{percentage+'%'}</span>;
};

const renderExplainability = (explainabilityFactors) => {
	let positiveFactors = [];
	let negativeFactors = [];
	explainabilityFactors.forEach((value)=>{
		if(value.percentage < 0){
			negativeFactors.push(value);
		}
		else{
			positiveFactors.push(value);
		}
	});
	return (<div>
		{
			positiveFactors.length > 0?
				<div className="positive-factors">
					<div
						className="positive factor-title"
						title={t('Positive factors')}
						role="heading"
						aria-level="5"
					>
						{t('Positive factors')}
					</div>
					<ul className="explainability-factors" role="list">
						{
							positiveFactors.map((predictiveFactor) => {
								return <li role="listitem" className="explainability-popover-pf-row">
									<span className="explainability-popover-pf-factor" 
										title={t(predictiveFactor.factor)}
									>
										{t(predictiveFactor.factor)}
									</span>
									<span>
										{renderValue(predictiveFactor.percentage)}
									</span>
								</li>;
							})
						}
					</ul>
				</div>:null
		}
		{
			negativeFactors.length > 0?
				<div className="negative-factors">
					<div
						className="negative factor-title"
						title={t('Negative factors')}
						role="heading"
						aria-level="5"
					>
						{t('Negative factors')}
					</div>
					<ul className="explainability-factors" role="list">
						{
							negativeFactors.map((predictiveFactor) => {
								return <li role="listitem" className="explainability-popover-pf-row">
									<span className="explainability-popover-pf-factor"
										title={t(predictiveFactor.factor)}
									>
										{t(predictiveFactor.factor)}
									</span>
									{renderValue(predictiveFactor.percentage)}
								</li>;
							})
						}
					</ul>
				</div>:null
		}
	</div>);
};

const view = ({properties: {explainabilityTitle, explainabilityFactors}}) => {
	return <div className="explainability-popover">
		<div className="explainability-popover-header">
			<div 
				className="explainability-popover-title"
				title={t(explainabilityTitle)}
				role="heading"
				aria-level="4"
			>
				{t(explainabilityTitle)}
			</div>
		</div>
		<slot name="popover"/>
		<div className="explainability-popover-content">
			{
				renderExplainability(explainabilityFactors)
			}
		</div>
	</div>;
};

createCustomElement('sn-explainability-popover', {
	renderer: {type: snabbdom},
	view,
	properties: {
		explainabilityTitle: {
			schema: {
				type: 'string'
			},
			default: 'Top predictive factors'
		},
		explainabilityFactors: {
			schema: {
				type: 'array'
			},
			default: []
		}
	},
	styles
});