import '@servicenow/now-heading';
import '@servicenow/now-icon';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import '@servicenow/now-score';
import {t} from 'sn-translate';
import './ettr-popover';
import './info-message-popover';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-loader';


const renderHeaderInfoIconWithMessage = (properties) => {
	let {infoMessage} = properties;
	if (infoMessage) {
		return (
		//TODO: fix info message not properly rendering issue with top-center, bottom-center config
			<now-popover hideTail={true} positions={[
				{ target: 'top-center', content: 'bottom-center' }
			]}>
				<now-button-iconic
					icon="info-circle-outline"
					className="ettr-card-header-icon"
					slot="trigger"
					size="md"
					variant="tertiary"
					hidePadding={true}
					configAria={{'button':{'aria-label':t('Info message')}}}
				/>
				<info-message-popover
					slot="content"
					infoMessage={infoMessage}
					aria-live="polite"
				></info-message-popover>
			</now-popover>
		);
	}
	else{
		return null;
	}
};

const renderCardHeader = (properties, targetRef, updateState) => {
	let {cardTitle, infoMessage} = properties;
	return <div className='ettr-card-header'>
		{
			cardTitle? 
				<now-heading
					label={cardTitle}
					level="3"
					variant="title-secondary"
					className="ettr-card-title"
					wont-wrap
					has-no-margin
				></now-heading> :
				null
		}
		{
			(infoMessage
			)? renderHeaderInfoIconWithMessage(properties, targetRef, updateState) : null
		}
	</div>;
};

const renderHighlightedMessage = (properties) => {
	let {slaMessage, slaMessageColor} = properties;
	if(!slaMessage) return null;

	return <now-highlighted-value className="ettr-card-highlighted-message" color={slaMessageColor} label={slaMessage} icon="info-circle-outline"></now-highlighted-value>;
};

const renderEstimation = (properties) => {
	let {estimationLabel, estimationValue} = properties;
	if(!estimationLabel && !estimationValue){
		return null;
	}
	return <div className="ettr-card-estimation">
		<slot name="content"/>
		<now-score-basic 
			heading={estimationLabel}
			score={estimationValue}
			headingPosition="top"
			timestamp=""
			scoreSize="sm" 
			hide-whitespace
		></now-score-basic>
	</div>;
};

const renderPopoverTrigger = (properties, isExplainabilityPopOverOpen, updateState) => {
	let {
		openExplainabilityLabel,
		closeExplainabilityLabel
	} = properties;
	let buttonLabel = openExplainabilityLabel;
	let buttonIcon = 'caret-down-fill';
	if(isExplainabilityPopOverOpen) {
		buttonLabel = closeExplainabilityLabel;
		buttonIcon = 'caret-up-fill';
	}
	return <now-button-bare 
		label={buttonLabel}
		iconEnd={buttonIcon}
		variant="secondary"
		size="sm"
		slot="trigger"
		className="details-button"
		on-click= {
			() => {
				updateState({
					path: 'isExplainabilityPopOverOpen',
					value: !isExplainabilityPopOverOpen,
					operation: 'set'
				});
			}
		}
	>
	</now-button-bare>;
};

const renderPopover = (properties, isExplainabilityPopOverOpen, updateState) => {
	return <now-popover hideTail={true} positions={[
		{ target: 'bottom-start', content: 'top-start' }
	]} append-to-payload={{
		isExplainabilityPopover: true
	}}>
		{renderPopoverTrigger(properties, isExplainabilityPopOverOpen, updateState)}
		<sn-explainability-popover
			slot="content"
			explainabilityTitle={properties.explainabilityTitle}
			explainabilityFactors={properties.explainabilityFactors}xxx
			aria-live="polite"
		></sn-explainability-popover>
	</now-popover>;
};

const renderETTRBody = (properties, isExplainabilityPopOverOpen, updateState) => {
	return <Fragment>
		{
			//main content
			renderEstimation(properties)
		}
		{
			//alert
			renderHighlightedMessage(properties)
		}
		{
			//popover
			properties.enableExplainability? renderPopover(properties, isExplainabilityPopOverOpen, updateState) : null
		}
	</Fragment>;
};

const renderErrorState = (errorMessage) => {
	return <div className="error-state">
		<now-highlighted-value color="critical" label={t('Unable to predict')} title={t('Unable to predict')} icon="info-circle-outline">
		</now-highlighted-value>
		<div className="error-message" title={errorMessage}>
			{errorMessage}
		</div>
	</div>;
};

const renderLoadingOrErrorStateHTML = ({isLoading, errorMessage})=>{
	if(isLoading)
		return <now-loader></now-loader>;
	if(errorMessage!='')
		return renderErrorState(errorMessage);
	return null;
};

const renderEttrCard = (properties, isExplainabilityPopOverOpen, updateState) => {
	return <div className="ettr-card">
		{
			renderCardHeader(properties, updateState)
		}
		{
			<hr className="ettr-card-divider"/>
		}
		{
			(properties.isLoading || properties.errorMessage != '')? renderLoadingOrErrorStateHTML(properties): renderETTRBody(properties, isExplainabilityPopOverOpen, updateState)
		}
	</div>;
};

const view = ({properties, isExplainabilityPopOverOpen}, {updateState}) => {
	return renderEttrCard(properties, isExplainabilityPopOverOpen, updateState);
};

export default view;